.dropdown {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.dropdown.is-right {
  justify-content: flex-end;
}
.dropdown.is-center {
  justify-content: center;
}
.dropdown-menu {
  position: absolute;
  top: 70px;
  left: 0;
  padding: var(--padding);
  width: 300px;
  border: calc( 2px + 2 * (100vw - 320px) / 2240 ) solid #000;
  border-radius: var(--borderRadius);
  background-color: #fff;
  color: #000;
  list-style: none;
  transform-origin: top left;
  user-select: none;
}
.dropdown-menu li {
  padding: 0;
  font-size: var(--font);
}
.dropdown-menu .dropdown-menu {
  position: relative;
  top: 0;
}
.dropdown-menu:empty {
  display: none;
}
.dropdown-menu.is-hidden {
  margin: 0;
  padding: 0;
  height: 0;
  transform: scale(0);
}
.dropdown-menu.is-visible {
  transform: scale(1);
}
.dropdown-menu:before {
  position: absolute;
  top: -0.5em;
  left: var(--padding);
  display: block;
  width: 0;
  height: 0;
  border-width: 0 0.5em 0.5em 0.5em;
  border-style: solid;
  border-color: transparent transparent #000 transparent;
  content: '';
}
.dropdown-menu:after {
  position: absolute;
  top: calc( -6px + 4 * (100vw - 320px) / 2240 );
  left: var(--padding);
  display: block;
  width: 0;
  height: 0;
  border-width: 0 0.5em 0.5em 0.5em;
  border-style: solid;
  border-color: transparent transparent #fff transparent;
  content: '';
}
.is-right .dropdown-menu {
  right: 0;
  left: auto;
  left: initial;
  transform-origin: top right;
}
.is-right .dropdown-menu:before,
.is-right .dropdown-menu:after {
  right: var(--padding);
  left: auto;
  left: initial;
}
.is-center .dropdown-menu {
  left: calc(50% - 150px);
  transform-origin: top center;
}
.is-center .dropdown-menu:before,
.is-center .dropdown-menu:after {
  left: calc(50% - 7px);
}
.modal {
